<template>
  <div>
    <b-container fluid>
      <b-row class="sideNav">
        <b-col cols="2" class="gauche">
          <SideNav />
        </b-col>
        <b-col cols="10" class="droite text-left">
          <SmallTopNav />
          <b-container fluid class="corp">
            <b-row class="titre mb-3 mt-5">
              <h1>Liste des entreprises</h1>
            </b-row>
            <b-row class="listMiniCartes">
              <b-col
                v-for="stage,index in stages"
                :key="index"
                cols="4"
                class="miniCarte"
              >
                <b-row class="nomMiniCarte">
                  <p>{{ stage.entreprise }}</p>
                </b-row>
                <b-row class="institutionMiniCarte">
                  <p>{{ stage.poste }}</p>
                </b-row>
                <b-row class="villeMiniCarte">
                  <p>{{ stage.ville }}</p>
                </b-row>
                <b-row class="bouttonMiniCarte">
                  <button 
                    @click="goTodetailStageAdmin(stage.stageId)"
                  >
                    <font-awesome-icon
                      id="editIcon"
                      class="iconeCarte"
                      icon="edit"
                      size="1x"
                    /><span>Modifier</span>
                  </button>
                  <button
                    v-on:click="supprimer(index)"
                  >
                    <font-awesome-icon
                      id="trashIcon"
                      class="iconeCarte"
                      icon="trash-alt"
                      size="1x"
                    /><span>Supprimer</span>
                  </button>
                </b-row>
              </b-col>
            </b-row>
          </b-container>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import SideNav from "../../layout/SideNav";
import SmallTopNav from "../../layout/SmallTopNav";
import json from "../../assets/data.json";
export default {
  name: "ListeStages",
  components: {
    SideNav,
    SmallTopNav,
  },
  data: function() {
    return {
      stages: json.stages, // Passer les données JSON
    };
  },
  //Pour faire suivre les détails vers la fiche détaillé
  methods: {
    goTodetailStageAdmin(stagdId) {
      let staId = stagdId;
      this.$router.push({ name: "FicheStage", params: { Sid: staId } });
    },
    supprimer (index) {
      this.$delete(this.stages , index)
    },
  },
};
</script>

<style lang="scss">
$bleuPrimaire: #0d6efd; //bleu officiel de bootstrap
$bleuSecondaire: #5297ff;
$grisArriereUn: #bdbdbd;
$grisArriereSombre: #7a7a7a;
$Blanc: #f8f8f8;
$Noir: #0c0c0c;
$Rouge: #e93939;
$BoxShadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
.listMiniCartes {
  .miniCarte {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: $BoxShadow;
    .nomMiniCarte {
      margin-top: 15px;
      p {
        color: $grisArriereSombre;
        font-size: 1.7rem;
        font-weight: bold;
        padding: 0;
      }
    }
    .institutionMiniCarte,
    .villeMiniCarte {
      p {
        color: $bleuPrimaire;
        font-size: 1rem;
        padding: 0;
        margin-bottom: 2px;
      }
    }
    .bouttonMiniCarte {
      button {
        background-color: $Blanc;
        border: none;
        box-shadow: $BoxShadow;
        padding: 5px 8px;
        margin: 12px 4px 20px 4px;
        border-radius: 5px;
        #editIcon {
          color: green;
          margin-right: 10px;
        }
        #trashIcon {
          color: $Rouge;
          margin-right: 10px;
        }
      }
    }
  }
}
</style>
